<template>
  <div class="tmpl">
    <nav-bar title="图文分享"></nav-bar>
    <!--引入返回导航-->
    <div class="photo-header">
      <ul>
        <li v-for="category in categorys" :key="category.id">
          <a href="javascript:;" @click="loadImg(category.id)">{{category.title}}</a>
        </li>
      </ul>
    </div>
    <div class="photo-list">
      <ul>
        <li v-for="img in imgList" :key="img.img_id">
          <router-link :to="{name:'photo.detail',params:{id:img.img_id}}">
            <!--懒加载-->
            <img v-lazy="img.img">
            <p>
              <span>{{img.img_title}}</span>
              <br>
              <span>{{img.desc}}</span>
            </p>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
    export default {
      name: "PhotoShare",
      data() {
        return {
          categorys: [],//分类列表
          imgList:[]//图文列表
        }
      },
      created(){
        //获取图文分类
        this.$axios.get(this.$host+'/api/getimgcategory/')
          .then(res=>{
            // console.log(res.data.data);
            this.categorys = res.data.data;
            //插入列表前面
            this.categorys.unshift({
              id:0,
              title:'全部'
            });
          });
        //获取图文
        this.loadImg(0);
      },
      methods:{
        loadImg:function (id) {
          this.$axios.get(this.$host+'/api/getimages/'+id)
            .then(res=>{
              // console.log(res.data.data);
              this.imgList = res.data.data;
            });
        }
      }
    }
</script>

<style scoped>
  .photo-header li{
    list-style: none;
    display: inline-block;
    margin-left: 10px;
    height: 30px;
  }
  .photo-header ul{
    /*强制不换行*/
    white-space: nowrap;
    overflow-x: auto;
    padding-left: 0px;
    margin: 5px;
  }
  .photo-list ul{
    padding-left: 0px;
    margin: 0px;
  }
  .photo-list li{
    position: relative;
    list-style: none;
    margin-top: 10px;
    width: 375px;
    height: 300px;
  }
  .photo-list li img{
    width: 100%;
    height: 230px;
    vertical-align: top;
  }
  .photo-list p{
    position: absolute;
    bottom: 0px;
    color: white;
    background-color: rgba(0,0,0,0.3);
    margin-bottom: 0px;
  }
  .photo-list span:nth-child(1){
    font-weight: bold;
    font-size: 16px;
  }
  /*图片懒加载样式*/
  image[lazy=loading] {
    width: 40px;
    height: 300px;
    margin: auto;
  }
</style>




